.chat {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  background-color: #f1f2f5;
  //&__header {
  //  display: flex;
  //  align-items: center;
  //  height: 80px;
  //  border-bottom: 1px solid #d9dfe1;
  //  flex-shrink: 0;
  //}
  &__header-text {
    padding-left: 30px;
    flex-grow: 1;
  }
  &__header-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 100%;
    background-color: transparent;
    border: none;
    border-left: 1px solid #d9dfe1;
    img {
      width: 25px;
      height: 25px;
      object-fit: contain;
    }
  }

  &__body {
    overflow-x: hidden;
    padding: 25px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__message {
    display: flex;
    align-items: flex-start;
    max-width: 80%;
    flex-shrink: 0;
  }
  &__user {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__user-icon {
    margin-bottom: 7px;
    display: block;
    width: 37px;
    height: 37px;
    object-fit: contain;
    border-radius: 50%;
  }
  &__msg-time {
    font-size: 11px;
    font-weight: 500;
    color: #616364;
  }
  &__msg-text {
    margin-bottom: 20px;
    padding: 15px;
    display: block;
    font-size: 16px;
    font-weight: 300;
    color: #000000;
    background-color: #fff;
    border-radius: 4px;
  }
  &__msg-from-interlocutor {
    .chat__user {
      margin-right: 10px;
    }
  }
  &__msg-from-my {
    flex-direction: row-reverse;
    margin-left: auto;
    .chat__user {
      margin-left: 10px;
    }
  }

  &__footer {
    margin-top: auto;
    padding: 15px;
    display: flex;
    align-items: flex-end;
    background-color: #fff;
    flex-shrink: 0;
  }
  &__add {
    position: relative;
  }
  &__add-btn {
    background-color: transparent;
    border: none;
    img {
      width: 25px;
      height: 25px;
      object-fit: contain;
    }
  }
  &__add-list {
    position: absolute;
    bottom: 250%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 150px;
    background-color: #fff;
    border-radius: 4px;
    &:before {
      content: '';
      position: absolute;
      left: 10px;
      bottom: -10px;
      display: block;
      width: 10px;
      border-top: 10px solid #fff;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
    }
  }
  &__add-item {
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    cursor: pointer;
    input[type="file"] {
      display: none;
    }
    img {
      width: 20px;
      height: 20px;
      object-fit: contain;
    }
  }
  &__input {
    flex-grow: 1;
  }
  &__input-field {
    min-height: 25px;
    max-height: 200px;
    overflow-x: hidden;
    cursor: text;
  }
  &__smile-btn {
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: none;
    flex-shrink: 0;
    img {
      width: 23px;
      height: 23px;
      object-fit: contain;
    }
  }
  &__send-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #ccc;
    border: none;
    border-radius: 50%;
    flex-shrink: 0;
    img {
      width: 23px;
      height: 23px;
      object-fit: contain;
    }
  }
  &__tab {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    &-logo {
      display: block;
      margin-right: 15px;
      width: 50px;
      height: 50px;
      object-fit: contain;
      border-radius: 50%;
    }
    &-title {
      font-size: 16px;
      font-weight: 600;
      color: #000;
    }
    &-actions {
      margin-left: auto;
    }
    &-del {
      display: flex;
      align-items: center;
      font-size: 12px;
      font-weight: 300;
      color: #000;
      background-color: transparent;
      border: none;
      img {
        margin: 5px;
        display: block;
        width: 10px;
        height: 10px;
        object-fit: contain;
      }
    }
    &--online {
      &:before {
        content: '';
        position: absolute;
        top: -2px;
        left: -2px;
        display: block;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background-color: #17ad27;
      }
    }
  }
}